.woocommerce-checkout.checkout {
	@apply grid grid-cols-1 lg:grid-cols-2 gap-6 w-full;

	#order_review_heading {
		@apply hidden;
	}

	.woocommerce-NoticeGroup-checkout {
		@apply col-span-full lg:col-span-2;
	}

	#customer_details {
		@apply col-span-1 lg:pr-12;

		h3 {
			@apply text-xl font-normal;
		}

		.woocommerce-shipping-fields {
			/* @apply mt-9; */
		}
	}

	#order_review {
		@apply relative mt-0 pt-0 pb-12 px-6 lg:px-12 lg:pt-9 bg-senary rounded-theme col-span-1 lg:mt-6;
	}

	#ship-to-different-address {
		@apply mb-8;
	}

	.form-row-first {
		@apply w-1/2 inline-block pr-2 my-0 !important;
	}

	.form-row-last {
		@apply w-1/2 inline-block pl-2 my-0 !important;
	}

	.form-row {
		&#billing_invoice_field {
			@apply mt-8;
		}

		&#billing_customer_type_field {
			@apply mt-6;
		}

		/* &#shipping_postcode_field,
        &#shipping_city_field,
        &#shipping_state_field {
            @apply w-1/3 inline-block mt-1;
        } */

		/* &#shipping_city_field {
            @apply px-4;
        } */
	}

	/* .woocommerce-shipping-fields__field-wrapper {
        border: 2px solid red;
    } */
}
